export const ABSTRA_URLS = {
  HOMEPAGE: 'https://abstra.io',
  DOCS: 'https://abstra.io/docs',
  CLOUD: 'https://cloud.abstra.io',
  GITHUB: 'https://github.com/abstra-app/abstra-lib',
} as const;

export const buildDocsUrl = (path?: string): string => {
  if (!path) return ABSTRA_URLS.DOCS;
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  return `${ABSTRA_URLS.DOCS}/${cleanPath}`;
};
